<template>
    <el-row style="margin-top: 20px; margin-bottom: 20px">
        <el-col :span="24">
            <div id="myChart" :style="{ width: '100%', height: '800px' }"></div>
        </el-col>
    </el-row>
</template>

<script>
import _ from 'lodash';
import request from '../../../utils/request.js';

