                        };
                    }
                    let myChart = this.$echarts.init(document.getElementById('myChart'));
                    // ÁªòÂà∂ÂõæË°®
                    var option = {
                        animationDurationUpdate: 1000, //Êï∞ÊçÆÊõ¥Êñ∞Âä®ÁîªÁöÑÊó∂Èïø„ÄÇ
                        animationEasingUpdate: 'quinticInOut', //Êï∞ÊçÆÊõ¥Êñ∞Âä®ÁîªÁöÑÁºìÂä®ÊïàÊûú„ÄÇ
                        color: ['#008000', '#cc6633', '#c00', '#ccc'],
                        grid: {
                            top: 0,
                            bottom: 0,
                            left: 0,
                            right: 0
                        },
                        series: [
                            {
                                type: 'graph', // Á±ªÂûã:ÂÖ≥Á≥ªÂõæ
                                layout: 'force', // ÂõæÁöÑÂ∏ÉÂ±ÄÔºåÁ±ªÂûã‰∏∫ÂäõÂØºÂõæ
                                focusNodeAdjacency: true, // ÂΩìÈº†Ê†áÁßªÂä®Âà∞ËäÇÁÇπ‰∏äÔºåÁ™ÅÂá∫ÊòæÁ§∫ËäÇÁÇπ‰ª•ÂèäËäÇÁÇπÁöÑËæπÂíåÈÇªÊé•ËäÇÁÇπ
                                draggable: true, // ÊåáÁ§∫ËäÇÁÇπÊòØÂê¶ÂèØ‰ª•ÊãñÂä®
                                symbolSize: 14, // Ë∞ÉÊï¥ËäÇÁÇπÁöÑÂ§ßÂ∞è
                                roam: 'scale', // ÊòØÂê¶ÂºÄÂêØÈº†Ê†áÁº©ÊîæÂíåÂπ≥ÁßªÊº´Ê∏∏„ÄÇÈªòËÆ§‰∏çÂºÄÂêØ„ÄÇÂ¶ÇÊûúÂè™ÊÉ≥Ë¶ÅÂºÄÂêØÁº©ÊîæÊàñËÄÖÂπ≥Áßª,ÂèØ‰ª•ËÆæÁΩÆÊàê 'scale' ÊàñËÄÖ 'move'„ÄÇËÆæÁΩÆÊàê true ‰∏∫ÈÉΩÂºÄÂêØ
                                edgeSymbol: ['circle', 'arrow'], // Ëæπ‰∏§Á´ØÁöÑÊ†áËÆ∞Á±ªÂûãÔºåÂèØ‰ª•ÊòØ‰∏Ä‰∏™Êï∞ÁªÑÂàÜÂà´ÊåáÂÆö‰∏§Á´ØÔºå‰πüÂèØ‰ª•ÊòØÂçï‰∏™Áªü‰∏ÄÊåáÂÆö„ÄÇÈªòËÆ§‰∏çÊòæÁ§∫Ê†áËÆ∞ÔºåÂ∏∏ËßÅÁöÑÂèØ‰ª•ËÆæÁΩÆ‰∏∫ÁÆ≠Â§¥ÔºåÂ¶Ç‰∏ãÔºöedgeSymbol: ['circle', 'arrow']
                                edgeSymbolSize: [2, 10], // Ëæπ‰∏§Á´ØÁöÑÊ†áËÆ∞Â§ßÂ∞èÔºåÂèØ‰ª•ÊòØ‰∏Ä‰∏™Êï∞ÁªÑÂàÜÂà´ÊåáÂÆö‰∏§Á´ØÔºå‰πüÂèØ‰ª•ÊòØÂçï‰∏™Áªü‰∏ÄÊåáÂÆö„ÄÇ
                                edgeLabel: {
                                    normal: {
                                        show: false,
                                        formatter: function (x) {
                                            return x.data.name;
                                        },
                                        textStyle: {
                                            fontSize: 14,
                                            color: '#666666'
                                        }
                                    }
                                },
                                force: {
                                    //ÂäõÂºïÂØºÂõæÂü∫Êú¨ÈÖçÁΩÆ
                                    layoutAnimation: true,
                                    // xAxisIndex : 0, //xËΩ¥ÂùêÊ†á ÊúâÂ§öÁßçÂùêÊ†áÁ≥ªËΩ¥ÂùêÊ†áÈÄâÈ°π
                                    // yAxisIndex : 0, //yËΩ¥ÂùêÊ†á
                                    gravity: 0.02, //ËäÇÁÇπÂèóÂà∞ÁöÑÂêë‰∏≠ÂøÉÁöÑÂºïÂäõÂõ†Â≠ê„ÄÇËØ•ÂÄºË∂äÂ§ßËäÇÁÇπË∂äÂæÄ‰∏≠ÂøÉÁÇπÈù†Êã¢„ÄÇ
                                    edgeLength: 60, //ËæπÁöÑ‰∏§‰∏™ËäÇÁÇπ‰πãÈó¥ÁöÑË∑ùÁ¶ªÔºåËøô‰∏™Ë∑ùÁ¶ª‰πü‰ºöÂèó repulsion„ÄÇ[10, 50] „ÄÇÂÄºË∂äÂ∞èÂàôÈïøÂ∫¶Ë∂äÈïø
                                    repulsion: 100 //ËäÇÁÇπ‰πãÈó¥ÁöÑÊñ•ÂäõÂõ†Â≠ê„ÄÇÊîØÊåÅÊï∞ÁªÑË°®ËææÊñ•ÂäõËåÉÂõ¥ÔºåÂÄºË∂äÂ§ßÊñ•ÂäõË∂äÂ§ß„ÄÇ
                                    // repulsion: 3000, //ËäÇÁÇπ‰πãÈó¥ÁöÑÊñ•ÂäõÂõ†Â≠ê„ÄÇÊîØÊåÅÊï∞ÁªÑË°®ËææÊñ•ÂäõËåÉÂõ¥ÔºåÂÄºË∂äÂ§ßÊñ•ÂäõË∂äÂ§ß„ÄÇ
                                    // edgeLength: 80 //ËæπÁöÑ‰∏§‰∏™ËäÇÁÇπ‰πãÈó¥ÁöÑË∑ùÁ¶ªÔºåËøô‰∏™Ë∑ùÁ¶ª‰πü‰ºöÂèó repulsion„ÄÇ[10, 50] „ÄÇÂÄºË∂äÂ∞èÂàôÈïøÂ∫¶Ë∂äÈïø
                                },
                                lineStyle: {
                                    normal: {
                                        width: 2,
                                        // color: '#4b565b',
                                        color: 'target', // ÂÜ≥ÂÆöËæπÁöÑÈ¢úËâ≤ÊòØ‰∏éËµ∑ÁÇπÁõ∏ÂêåËøòÊòØ‰∏éÁªàÁÇπÁõ∏Âêå
                                        curveness: 0.2,
                                        type: 'solid' // 'dotted'ÁÇπÂûãËôöÁ∫ø 'solid'ÂÆûÁ∫ø 'dashed'Á∫øÊÄßËôöÁ∫ø
                                    }
                                },
                                label: {
                                    show: true,
                                    position: 'top',
                                    formatter: '{b}',
                                    textStyle: {
